	data() {
		return {
			temp: {},
			isfirst: true,
			headerPosition: 'fixed',
			headerTop: null,
			statusTop: null,
			//个人信息,
			user: {
				username: 'Love clean',
				face: '../../static/img/log.png',
				signature: '我的个性签名。',
				integral: 0,
				balance: 0,
				envelope: 0
			},
			// 订单类型
			orderList: [
				{ text: '待付款', icon: 'fukuan' },
				{ text: '待发货', icon: 'fahuo' },
				{ text: '待收货', icon: 'shouhuo' },
				{ text: '已完成', icon: 'pingjia' },
				{ text: '已取消', icon: 'tuihuo' }
			],
			// 工具栏列表
			mytoolbarList: [
				{ text: '我的订单', img: '../../static/img/user/point.png' },
				{ text: '优惠券', img: '../../static/img/user/quan.png' },
				{ text: '新客豪礼', img: '../../static/img/user/renw.png' },
				// {text:'领红包',img:'../../static/img/user/momey.png'},

				{ text: '收货地址', img: '../../static/img/user/addr.png' }
				// {text:'账户安全',img:'../../static/img/user/security.png'},
				// {text:'银行卡',img:'../../static/img/user/bank.png'},
				// {text:'抽奖',img:'../../static/img/user/choujiang.png'},
				// {text:'客服',img:'../../static/img/user/kefu.png'},
				// { text: '开票申请', img: '../../static/img/user/mingxi.png' }
			]
		};
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop >= 0 ? 'fixed' : 'absolute';
		this.headerTop = e.scrollTop >= 0 ? null : 0;
		this.statusTop = e.scrollTop >= 0 ? null : -this.statusHeight + 'px';
	},
	onShareAppMessage(res) {
		return {
			title: '来一桶水小程序，订水又快又方便',
			path: '/pages/tabBar/cart',
			imageUrl: '../../static/img/logo.jpg'
		};
	},
	onLoad() {
		uni.getSystemInfo({
			success: res => {
				this.temp = res;
				console.log(res.model);
			}
		});

		uni.showShareMenu({
			withShareTicket: true
		});
		this.temp.level = wx.getBatteryInfoSync().level;
		this.temp.isCharging = wx.getBatteryInfoSync().isCharging;

		this.statusHeight = 0;



	},
	onReady() {
		//此处，演示,每次页面初次渲染都把登录状态重置
		uni.setStorage({
			key: 'UserInfo',
			data: false,
			success: function() {},
			fail: function(e) {}
		});
	},
	onShow() {
		uni.getStorage({
			key: 'UserInfo',
			success: res => {
				if (!res.data) {
					if (this.isfirst) {
						//this.toLogin();
					}
					return;
				}
				this.user = res.data;
			},
			fail: e => {
				//this.toLogin();
			}
		});
	},
	methods: {
		box(row) {
			if (row.text === '我的订单') {
				uni.navigateTo({
					url: '../user/order_list/order_list_temp?tbIndex=-1'
				});
			} else if (row.text === '优惠券') {
				uni.navigateTo({
					url: '../user/coupon/coupon'
				});
			} else if (row.text === '新客豪礼') {
				uni.navigateTo({
					url: '../user/coupon/getCoupon?couponsId=1'
				});
			} else if (row.text === '收货地址') {
				uni.navigateTo({
					url: '../address/address'
				});
			}
		},
		//消息列表
		toMsg() {
			uni.navigateTo({
				url: '../msg/msg'
			});
		},
		toOrderList(index) {
			// uni.showToast({ title: '功能还没有做好呢', icon: 'none' });
			uni.navigateTo({ url: '../user/order_list/order_list_temp?tbIndex=' + index });
		},
		toSetting() {
			uni.navigateTo({
				url: '../user/setting/setting'
			});
		},
		toMyQR() {
			uni.navigateTo({
				url: '../user/myQR/myQR'
			});
		},
		toLogin() {
			uni.showToast({ title: '请登录', icon: 'none' });
			uni.navigateTo({
				url: '../login/login'
			});
			this.isfirst = false;
		},
		isLogin() {
			//实际应用中,用户登录状态应该用token等方法去维持登录状态.
			const value = uni.getStorageSync('UserInfo');
			if (value) {
				return true;
			}
			return false;
		}
	}
};
